.modal {
  width: 480px;
  height: 459px;
  border-radius: 12px;
  background: #c2f5ff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.height634 {
  height: 634px;
}

.image {
  width: 96px;
  height: 96px;
  margin-bottom: 8px;
}

.title {
  max-width: 270px;

  text-align: center;
  color: #004980;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: StratosSkyeng;
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: 48px;

  margin-bottom: 28px;
}

.description {
  color: #000;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: StratosSkyeng;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;

  margin-bottom: 10px;
}

.time {
  color: #000;
  font-family: StratosSkyeng;
  font-size: 64px;
  font-style: normal;
  font-weight: 400;
  line-height: 72px;

  margin-bottom: 40px;
}

.input {
  width: 276px;
  height: 45px;
  margin-bottom: 24px;

  border-radius: 10px;
  text-align: center;
  font-family: StratosSkyeng;
  font-size: 24px;
}

.link {
  margin-top: 24px;
  font-size: 18px;
}

.submitButton {
  display: block;
  width: 51px;
  height: 51px;
  margin-left: 10px;
  padding: 0;

  appearance: none;
  cursor: pointer;
  color: transparent;
  background-color: transparent;
  border: none;
  border-top-left-radius: 15px 5px;
  border-bottom-left-radius: 15px 5px;
  border-left: 50px solid #7ac100;
  border-top: 23px solid transparent;
  border-bottom: 23px solid transparent;
}
.submitButton:hover {
  border-left: 50px solid #7ac100cc;
}
